import gql from 'graphql-tag'


export const createUserTicket = gql`
  mutation CreateUserTicket($input: CreateUserTicketInput!) {
    createUserTicket(input: $input) {
      id
      ticketID
      userID
    }
  }
`

export const createConvo = gql`
  mutation CreateConvo($input: CreateConversationInput!) {
    createConvo(input: $input) {
      id
      name
      createdAt
      associated {
        items {
          convoLinkUserId
          user {
            id
            username
          }
        }
      }
    }
  }
`

export const createTicket = gql`
  mutation CreateTicket($input: CreateTicketInput!) {
    createTicket(input: $input) {
      id
      countAndId
      ticketUserId
      owner
      content
      time
      destination
      price
      title
      county
      source
      status
      ticketEmployeeInfo
      ticketEmployeeId
      ticketUserInfo
      ownerUploadImages
      employeeUploadImages
      closedComment
      ticketType
      execptedPrice
    }
  }
`
export const updateTicket = gql`
    mutation UpdateTicket($input: UpdateTicketInput!
      ) {
      updateTicket(
        input: $input
      )
    {
      id
      content
      time
      destination
      price
      title
      county
      source
      status
      ownerUploadImages
      employeeUploadImages
      ticketEmployeeId
      closedComment
      closedTime
      ticketType
      execptedPrice
    }
  }
  `

export const assignExpertToTicket = gql`
    mutation UpdateTicket($id: ID!, $ticketEmployeeId: ID!, $price: String, $ticketEmployeeInfo: String) {
      updateTicket(
        input: {
          id: $id
          price: $price
          ticketEmployeeId: $ticketEmployeeId
          ticketEmployeeInfo: $ticketEmployeeInfo
          status: "assinged"
        }
      )
    {
      id
      content
      time
      destination
      price
      title
      county
      source
      status
      ticketEmployeeId
      ticketType
      execptedPrice
    }
  }
`

