import ampConfig from '../aws-exports'
// @ts-ignore
import { uuid } from 'uuidv4';
import imageCompression from 'browser-image-compression';
import { Storage, Cache} from 'aws-amplify'

const {
  aws_user_files_s3_bucket_region: region,
  aws_user_files_s3_bucket: bucket
} = ampConfig;


async function uploadImage(file: any) {

    if (file) {
        
        const { name: fileName, type: mimeType } = file  
        const key = `${uuid()}${fileName}`
        const fileForUpload = {
            bucket,
            key,
            region,
        }

        try {
            await Storage.put(key ,file, {
                contentType: mimeType
            })
          console.log('successfully stored user data!')
          return fileForUpload;
        } catch (err) {
          console.log('error: ', err)
        }
    }
}

export async function getImages(imageFiles: any[]) {
    console.log('getImages!', imageFiles);

    // map array to promises
    const promises = imageFiles.map(getImage);
    // wait until all promises are resolved
    const result = await Promise.all(promises);
    console.log('getImages!', result);
    return result;
}

async function getImage(imageFiles: any) {
    const avatarUrl = await Storage.get(imageFiles.key )
    return avatarUrl;
}

export async function getCacheImage(images: any) {
  let fileUrl = Cache.getItem(images.key)
		  if (fileUrl) {
      console.log(`Retrieved cached url for ${images.key}: ${fileUrl}`)
      return fileUrl[0];
		  }
	
		  fileUrl = await getImages([images]);
		  console.log(`Retrieved fetched url for ${images.key}: ${fileUrl}`)

		  const expires = new Date(new Date().getTime() + 14*60000).getTime();
      Cache.setItem(images.key, fileUrl, { expires })
      
      return fileUrl[0];
}


export async function handleImageUpload(imageFiles: any[]) {

  // map array to promises
  const promises = imageFiles.map(compress);
  // wait until all promises are resolved
  const result = await Promise.all(promises);
  console.log('Done!', result);
  return result;
}

async function compress(imageFile: any) {

        console.log('originalFile instanceof Blob', imageFile instanceof Blob); // true
        console.log(`originalFile size ${imageFile.size / 1024 / 1024} MB`);
      
        const options = {
          maxSizeMB: 1,
          maxWidthOrHeight: 1920,
          useWebWorker: false
        }
        try {
          const compressedFile = await imageCompression(imageFile, options);
          console.log('compressedFile instanceof Blob', compressedFile instanceof Blob); // true
          console.log(`compressedFile size ${compressedFile.size / 1024 / 1024} MB`); // smaller than maxSizeMB
          return await uploadImage(compressedFile); // write your own logic
        } catch (error) {
            console.log(error);
          }

  }