import React, { useRef } from 'react';
import usePlacesAutocomplete, { getGeocode, getLatLng, GeocodeResult } from 'use-places-autocomplete';
//import useOnclickOutside from 'react-cool-onclickoutside';
import TextField from '@material-ui/core/TextField';

 interface LocationSearchProps {
    googleAddressReturn: (value: GeocodeResult) => void;
    label: string;
  }
  
    const LocationSearchInput: React.SFC<LocationSearchProps> = (props) => {        
  const {
    ready,
    value,
    suggestions: { status, data },
    setValue,
    clearSuggestions
  } = usePlacesAutocomplete({
    requestOptions: { /* Define search scope here */ },
    debounce: 500
  });
   const ref = useRef();
//   useOnclickOutside(ref, () => {
//     // When user clicks outside of the component, we can dismiss
//     // the searched suggestions by calling this method
//     clearSuggestions();
//   });

  const handleInput = (e: any) => {
    // Update the keyword of the input element
    setValue(e.target.value);
  };

  // @ts-ignore
  const handleSelect = ({ description }) => () => {
    // When user selects a place, we can replace the keyword without request data from API
    // by setting the second parameter as "false"
    setValue(description, false);
    clearSuggestions();

    // Get latitude and longitude via utility functions
    getGeocode({ address: description })
      .then(results => 
        {
            console.log(results);
            const { lat, lng } =  results[0].geometry.location
            console.log(' Coordinates: ', results[0].geometry.location);

            results[0].latitudes = lat.toString()
            results[0].longitudes = lng.toString()
            props.googleAddressReturn(results);
            return getLatLng(results[0]).then(({ lat, lng }) => {
                console.log(' getLatLng: ', results);

                results[0].latitudes = lat.toString()
            results[0].longitudes = lng.toString()
            props.googleAddressReturn(results);
            })
            
        })
      .catch(error => {
        console.log(' Error: ', error)
      });

  };

  const renderSuggestions = () =>
    data.map(suggestion => {
      const {
        id,
        structured_formatting: { main_text, secondary_text }
      } = suggestion;

      return (
        <li
          key={id}
          onClick={handleSelect(suggestion)}
        >
          <strong>{main_text}</strong> <small>{secondary_text}</small>
        </li>
      );
    });

  return (
    <div>

    <TextField
            required
            label={props.label}
            fullWidth
            disabled={!ready}
            onChange={handleInput}
            value={value}
            autoComplete="billing address-level2"
          />
      {/* We can use the "status" to decide whether we should display the dropdown or not */}
      {status === 'OK' && <ul>{renderSuggestions()}</ul>}
    </div>
  );
};

export default LocationSearchInput;