import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import TextField from '@material-ui/core/TextField';
import { DialogTitle } from '../utils/DialogTitle';
import Rating, { IconContainerProps } from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import SentimentVeryDissatisfiedIcon from '@material-ui/icons/SentimentVeryDissatisfied';
import SentimentDissatisfiedIcon from '@material-ui/icons/SentimentDissatisfied';
import SentimentSatisfiedIcon from '@material-ui/icons/SentimentSatisfied';
import SentimentSatisfiedAltIcon from '@material-ui/icons/SentimentSatisfiedAltOutlined';
import SentimentVerySatisfiedIcon from '@material-ui/icons/SentimentVerySatisfied';
import { withStyles } from '@material-ui/core/styles';

const customIcons: { [index: string]: { icon: React.ReactElement; label: string } } = {
  1: {
    icon: <SentimentVeryDissatisfiedIcon />,
    label: 'Very Dissatisfied',
  },
  2: {
    icon: <SentimentDissatisfiedIcon />,
    label: 'Dissatisfied',
  },
  3: {
    icon: <SentimentSatisfiedIcon />,
    label: 'Neutral',
  },
  4: {
    icon: <SentimentSatisfiedAltIcon />,
    label: 'Satisfied',
  },
  5: {
    icon: <SentimentVerySatisfiedIcon />,
    label: 'Very Satisfied',
  },
};

const StyledRating = withStyles({
  iconFilled: {
    color: '#ff6d75',
  },
  iconHover: {
    color: '#ff3d47',
  },
})(Rating);

function IconContainer(props: IconContainerProps) {
  const { value, ...other } = props;
  return <span {...other}>{customIcons[value].icon}</span>;
}

interface AlertDialogProps {
    openPop: boolean;
    type: string
    closeAlert: (op: boolean, data: string) => void;
  }

 const AlertDialog: React.SFC<AlertDialogProps> = ({ openPop,type,  closeAlert}) => {

    const [disable, setDisable] = React.useState<boolean>(true)
    const [textValue, setTextValue] =  React.useState<string>("");

  const handleInputChange = (e: any) => {
    const {name, value} = e.target
    setTextValue(value)
    if(textValue && textValue.length > 0){
        setDisable(false)
    }
}

const handleOperation = () => {
    closeAlert(true, textValue);
  };

  const handleClose = () => {
    closeAlert(false, "");
  };

  return (
    <div>
      <Dialog
        open={openPop}
        keepMounted
        fullWidth ={true}
        maxWidth="lg"
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
          {
              type == "closed" ?(< div>
              <DialogTitle id="alert-dialog-slide-title" onClose={handleClose} > Close Ticket</DialogTitle>
              <DialogContent>
                <DialogContentText id="alert-dialog-slide-description">
                Do you want close ticket? <br></br>
                Reasons for cancellation.
                </DialogContentText>
                <TextField
          multiline
          rows="4"
          required
          id="content"
          autoFocus
          name="content"
          label="Close ticket"
          fullWidth
          value={textValue}
          onChange={handleInputChange}
          autoComplete="content"
        />

      <Box component="fieldset" mb={3} borderColor="transparent">
        <Typography component="legend">Rating</Typography>
        <Rating
          name="Rating"
          defaultValue={2}
          getLabelText={(value: number) => customIcons[value].label}
          IconContainerComponent={IconContainer}
        />
      </Box>

              </DialogContent>
              <DialogActions>
                <Button variant="contained" disabled={disable} onClick={handleOperation} color="secondary">
                  Close Ticket
                </Button>
              </DialogActions> </div>) : 

              (< div>
                <DialogTitle id="alert-dialog-slide-title" onClose={handleClose} >{type}</DialogTitle>
                <DialogContent>
                <DialogContentText id="alert-dialog-slide-description">
                {type== "completed" ? "Completed" : "Unassign"} comments .
                </DialogContentText>
            <TextField
          multiline
          rows="6"
          required
          autoFocus
          id="content"
          name="content"
          label={type== "completed" ? "Completed" : "Unassign"}
          fullWidth
          value={textValue}
          onChange={handleInputChange}
          autoComplete="content"
        />


                </DialogContent>
                <DialogActions>
                  <Button variant="contained" disabled={disable} onClick={handleOperation} color="primary">
                  {type== "completed" ? "Completed" : "Unassign"}
                  </Button>
                </DialogActions> </div>)

          }

      </Dialog>
    </div>
  );
}

export default AlertDialog;