import React, { useContext, useEffect } from 'react'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import AppHomeLayout from '../app/AppHomeLayout'
import { API, graphqlOperation } from 'aws-amplify'
import { getUserConversations, getUserMessages } from '../graphql/queries'
import { AuthContext } from '../auth/auth-context'
import {conversationLink, Conversation, getCacheImage, SelectedConv} from '../utils'
import './chat1.css';
import {ChatRoom} from './chat1'
import { useLocation } from "react-router-dom";
import Grid from '@material-ui/core/Grid';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import CommentIcon from '@material-ui/icons/Comment';
import Badge from '@material-ui/core/Badge';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
     // maxWidth: 400,
      backgroundColor: theme.palette.background.paper,
    },
    inline: {
      display: 'inline',
    },
    button: {
       width : 20,
       marginTop: 15,
       padding: 5,
       margin: theme.spacing(1),
    },
    rating: {
        marginLeft: 15
    },
    buttonCart: {
        marginLeft: 5,
        marginTop: 15,
        width: 10
    },
    buttonGird: {
        width: 90
    },
    cartLabel: {
        width: 10,
        marginBottom: 8,
        marginLeft: 5
    }
  }),
);

export default function ChatList() {
  const classes = useStyles();
  const [converstaions, setConverstaions] = React.useState([]);
  const authContext = useContext(AuthContext)
  const [flag, setFlagValue] = React.useState(false);
  const [selectedConv, setSelectedConv] = React.useState<SelectedConv>({
    convId: '', senderUsername: '',
    owner: '', ownerAvatar: '', senderAvatar: '', sender: ''
  });

  const [openChats , setOpenChats ] = React.useState<string[]>([]);
  const [ownerAvatar, setOwnerAvatar] = React.useState({});

  const location = useLocation();
    
  useEffect(() => {
    // console.log(location.pathname); // result: '/secondpage'
    // console.log(location.search); // result: '?query=abc'
   // console.log(location.state.deail); // result: 'some_value'

    const fetchData = async (location: any) => {

      //@ts-ignore
    if(location.state && location.state.conversation ){
      console.log(location); 

       //@ts-ignore
       const conversation = location.state.conversation.conversation as  Conversation  ;
              //@ts-ignore
       const currentId = location.state.user as  string  ;
      const user = conversation.associated.items.filter(conv => (conv.convoLinkUserId ==  currentId || conv.convoLinkEmployeeId ==  currentId  ))
      const otherUser = conversation.associated.items.filter(conv => !(conv.convoLinkUserId ==  currentId || conv.convoLinkEmployeeId ==  currentId  ))
      console.log("selected file",user, otherUser); 

      const useravatar = JSON.parse(user[0].user.avatar);
      const ownerAvatar = await getCacheImage(useravatar[0]);
      setOwnerAvatar(ownerAvatar);

      const otherUseravatar = JSON.parse(otherUser[0].user.avatar);
      const senderAvatar = await getCacheImage(otherUseravatar[0]);

      setSelectedConv({
        convId: conversation.id,
         owner: currentId,
         ownerAvatar: ownerAvatar,
         senderAvatar: senderAvatar,
         senderUsername:otherUser[0].user?.username ?? otherUser[0].employee?.username ,
         sender: otherUser[0].convoLinkUserId 
       });
       setFlagValue(true);
    }

    };

    if(location){
      fetchData(location);
    } 
 }, [location]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const id = authContext.userId;
        const type = authContext.user.attributes["custom:account_type"];

          const commentData = await API.graphql(graphqlOperation(getUserConversations, { id: id }))
         console.log("commentData", commentData);
          // @ts-ignore 
          if(commentData.data.getUser && commentData.data.getUser.userConversations.items.length > 0){
          // @ts-ignore 
          const comments = commentData.data.getUser.userConversations.items;

        // @ts-ignore 
        const useravatar = JSON.parse(commentData.data.getUser.avatar);
        const getImage = await getCacheImage(useravatar[0]);
        setOwnerAvatar(getImage);

        for(var i = 0; i < comments.length; i++) {
          const chat = comments[i];
          const otherUser = chat.conversation.associated.items.filter((conv : any)=> !(conv.convoLinkUserId ==  id || conv.convoLinkEmployeeId ==  id  ))
          const images = otherUser[0].user?.avatar ?? otherUser[0].employee?.avatar ;

          comments[i].avatar = await getCacheImage(JSON.parse(images)[0]);
         //console.log("images",images);
         comments[i].username = otherUser[0].user?.username ?? otherUser[0].employee?.username ;
         comments[i].receiver = otherUser[0].convoLinkEmployeeId ?? otherUser[0].convoLinkUserId ;
        }
          setConverstaions(comments);
          }

      const IgetUserMessages = await API.graphql(graphqlOperation(getUserMessages, { receiver: id, isSent: "false" }))
        //@ts-ignore
         if(IgetUserMessages.data.messageByReceiver && IgetUserMessages.data.messageByReceiver.items.length > 0){
                  //@ts-ignore
          const links = IgetUserMessages.data.messageByReceiver.items;
          let chatsLinks:string[] = []
          links.forEach((link:any) =>{
            chatsLinks.push(link.owner);
          }); 
          console.log("commentschatsLinks", chatsLinks, converstaions);
          setOpenChats(chatsLinks);

      }

      } catch (error) {
        console.log('unable get conversation for users:', error)
      }
    };
    if(authContext.userId){
      fetchData();
    }
  }, [authContext.userId]);

  const handleClick = (value: conversationLink) => () => {
    const currentId = authContext.userId;
    //const otherUser = value.conversation.associated.items.filter(conv => !(conv.convoLinkUserId ==  currentId || conv.convoLinkEmployeeId ==  currentId  ))

    setSelectedConv({
      convId: value.conversation.id,
      owner: authContext.userId,
      ownerAvatar: ownerAvatar as string,//"https://i.pravatar.cc/150?img=56",
      senderAvatar: value.avatar,
      senderUsername: value.username,
      sender: value.receiver//otherUser[0].convoLinkEmployeeId ?? otherUser[0].convoLinkUserId 
    });
    setFlagValue(true);
  };

  const backButton = () => {
    setFlagValue(false);

  }

  return (
    <div>
    { flag == false ?(
      <AppHomeLayout title='RA2 Serverless'>

    <List className={classes.root}>

    {converstaions.length > 0 ? converstaions.map((item: conversationLink,index) => (
      <div key={item.id} >
      <ListItem  key={item.id}  alignItems="flex-start" dense button onClick={handleClick(item)}>
      <ListItemAvatar>
        <Avatar alt="Remy Sharp" src={item.avatar} />
      </ListItemAvatar>
      <ListItemText
        primary={item.username}
      />
      <ListItemSecondaryAction>
              <IconButton edge="end" aria-label="comments">
                <Badge color="secondary" overlap="circle" badgeContent=" "
            invisible={openChats.includes(item.receiver)? false: true}
            >
    <CommentIcon color='action'/>
    </Badge>
    
              </IconButton>
            </ListItemSecondaryAction>
    </ListItem>
    <Divider variant="inset" component="li" />

      </div>  
    )): <Grid
    container
    direction="row"
    justify="center"
    alignItems="center"
  >
    <div>No Message Available </div>
    </Grid>
    }
    </List>
    </AppHomeLayout>)
    : 
    <div>
      	{/* <div className="chatApp__loaderWrapper">
		<div className="chatApp__loaderText">Loading...</div>
		<div className="chatApp__loader"></div>
	</div> */}
      <ChatRoom selectedConv={selectedConv} backButton={backButton}>

  </ChatRoom >

</div>
    }
    </div>

  );
}