/* detect url in a message and add a link tag */
function detectURL(message: any) {
	var urlRegex = /(((https?:\/\/)|(www\.))[^\s]+)/g;
	return message.replace(urlRegex, function(urlMatch:any) {
		return '<a href="' + urlMatch + '">' + urlMatch + '</a>';
	})
}

