/* ========== */
/* InputMessage component - used to type the message */
let uploadFile: any = null;
type InputMessageProp = {
    isLoading: boolean
    owner: string,
    ownerAvatar: string,
    sendMessageLoading: ( message: any)  => void,
	sendMessage: ( message: any)  => void,
	senderUsername: string
  }


  type InputMessageState = {
	messageInput: any,
	content: string,
	popoverOpen: boolean,
	file: any,
	filePreviewSrc: any
  }
class InputMessage extends React.Component<InputMessageProp, InputMessageState> {
	constructor(props: any) {
		super(props);
		this.state = {
			popoverOpen: false,
			content: '',
			messageInput: '',
			file: '',
			filePreviewSrc: ''
		};
		this.handleSendMessage = this.handleSendMessage.bind(this);



	}

	messageInput: any;

	ownerInput: any;
	ownerAvatarInput :any;
	handleSendMessage(event: any) {
		event.preventDefault();
		/* Disable sendMessage if the message is empty */
		if( uploadFile ||this.messageInput.value.length > 0 ) {
			this.props.sendMessageLoading( this.messageInput.value);
			/* Reset input after send*/
			this.messageInput.value = '';
			this.close();
		}
	}
	
	handleFileChange = (e: any) => {
		const file = e.target.files[0]
		console.log(file)
		if (file && file.type.startsWith('image/')) {
		  const reader = new FileReader()
		  const self = this
		  reader.onload = function(e) {
			self.setState({ popoverOpen: true, filePreviewSrc: this.result })
		  }
		  reader.readAsDataURL(file)
		  this.setState({ file })
		  uploadFile = file;
		}
	  }

	  close = () => {
		this.setState({ file: undefined, popoverOpen: false })
		uploadFile = null;
	  }
	

	render() {
		const { filePreviewSrc } = this.state;
		const imgBtnClass ='btn btn-block ' + (this.state.file ? 'btn-success' : 'btn-primary')

		/* If the chatbox state is loading, loading class for display */
		var loadingClass = this.props.isLoading ? 'chatApp__convButton--loading' : '';
		let sendButtonIcon =     <SendIcon color='action'/>
		//<i className={"material-icons"}>send</i>;
		return (
			<form onSubmit={this.handleSendMessage}>
                 <div className={'chatApp__convButtonLeft' + loadingClass}> 
                  {/* <input
				      className={'zeroWidth'}
                      type="file"
                      onChange={this.handleFileChange}
					/> */}
					
					<input accept="image/*" className={'zeroWidth'} id="icon-button-file" onChange={this.handleFileChange} type="file" />
      <label className={'fileUploadIcon'} htmlFor="icon-button-file">
        <IconButton color="primary" className={'fileUploadIcon'} aria-label="upload picture" component="span">
          <AttachmentIcon className={'fileUploadIcon'}  />
        </IconButton>
      </label>

				<label className={imgBtnClass} id="Popover1">
                      <i className="fas fa-camera" />
                    </label>
				    <Popover
                      placement="top-start"
                      isOpen={this.state.popoverOpen}
                      target="Popover1"
                    >
                      <PopoverHeader>
                        <span>Preview</span>
                        <button
                          type="button"
                          className="close"
                          aria-label="Close"
                          onClick={this.close}
                        >
                          <span aria-hidden="true">&times;</span>
                        </button>
                      </PopoverHeader>
                      <PopoverBody>
                        <img
                          alt="previw"
                          className="msg-image"
                          src={filePreviewSrc}
                        />
                      </PopoverBody>
                    </Popover>
				</div>
				<input
					type="hidden"
					ref={owner => (this.ownerInput = owner)}
					value={this.props.owner}
				/>
				<input
					type="hidden"
					ref={ownerAvatar => (this.ownerAvatarInput = ownerAvatar)}
					value={this.props.ownerAvatar}
				/>
				<input
					type="text"
					ref={message => (this.messageInput = message)}
					className={"chatApp__convInput"}
					placeholder="Text message"
				/>
				<div className={'chatApp__convButton ' + loadingClass} onClick={this.handleSendMessage}>
				{sendButtonIcon}
				</div>
			
			</form>
		);
	}
}
/* end InputMessage component */
/* ========== */


