/* ========== */
/* MessageItem component - composed of a message and the sender's avatar */
type MessageItemProp = {
    owner: string,
    sender: string
	senderAvatar: string
	ownerAvatar: string
	message: string,
	file: string;
  }

  type MessageItemState = {
	fileUrl: any,
	key: string
  }
class MessageItem extends React.Component<MessageItemProp, MessageItemState> {

	constructor(props: any) {
		super(props);
		this.state = {
			fileUrl: null,
			key: ""
		};
	}

	componentDidUpdate(prevProps: any, prevState: any) {
		const { file: prevMsg } = prevProps
		const { file: nodebugImage } = this.props
		let currMsg = null;
		if(nodebugImage){
			currMsg = JSON.parse(nodebugImage);
		}

		if (
		  prevMsg &&
		  prevMsg.key === null &&
		  currMsg &&
		  currMsg.key
		) {
		  this.checkFileUrl()
		}
	  }

	  async componentDidMount() {
		await this.checkFileUrl()
	  }

	  async checkFileUrl() {
		const {file} = this.props;

		if (file) {
		let nonFile = JSON.parse(file);
        const images = Array.isArray(nonFile) ? nonFile[0] : nonFile ;
		  let fileUrl = Cache.getItem(images.key)
		  if (fileUrl) {
			this.setState({ key: images.key })
			return this.setState({ fileUrl })
		  }
	
		  fileUrl = await getImages([images]);
		  const expires = new Date(new Date().getTime() + 14*60000).getTime();
		  Cache.setItem(images.key, fileUrl, { expires })
		  this.setState({ fileUrl })

		//   Storage.get(keyWithoutPrefix, { identityId }).then(fileUrl => {
		// 	console.log(`New url for ${file.key}: ${fileUrl}`)
		
		// 	const expires = new Date(new Date().getTime() + 14*60000).getTime();
		// 	Cache.setItem(file.key, fileUrl, { expires })
		// 	this.setState({ fileUrl })
		//  })
		}
	  }

	// 
	 senderAvatar =  this.props.owner == this.props.sender ? this.props.ownerAvatar : this.props.senderAvatar ;
	render() {

		/* message position formatting - right if I'm the author */
		let messageRight = (( this.props.owner == this.props.sender ) ? 'highlight msg-right' : 'highlight');

		let messagePosition = (( this.props.owner == this.props.sender ) ? 'chatApp__convMessageItem--right' : 'chatApp__convMessageItem--left');
		return (
			<div className={"chatApp__convMessageItem " + messagePosition + " clearfix"}>
				<img src={this.senderAvatar} alt={this.props.sender} className="chatApp__convMessageAvatar" /> 
				{
					this.props.file && this.state.fileUrl? (<div className={messageRight}>
					<img
					  alt="awesome"
					  src={this.state.fileUrl}
					  className="msg-image"
					  id="ImgPopover"
					/>
				  </div>):
				<div className="chatApp__convMessageValue" dangerouslySetInnerHTML={{__html: this.props.message}}></div>

				}
			</div>
		);
	}
}
/* end MessageItem component */
/* ========== */

/* ========== */
/* ChatBox component - composed of Title, MessageList, TypingIndicator, InputMessage */

type ChatBoxProp = {
    messages: any
    owner: string,
	ownerAvatar: string,
	senderAvatar: string,
	sendMessage: ( message: any) => void;
	backButton: () => void;
	senderUsername : string;
  }

  type ChatBoxState = {
    isLoading: boolean
  }

class ChatBox extends React.Component<ChatBoxProp, ChatBoxState> {
	constructor(prop: any) {
		super(prop);
		this.state = {
			isLoading: false
		};
		this.sendMessageLoading = this.sendMessageLoading.bind(this);
		var timeout = null;
	}
	/* catch the sendMessage signal and update the loading state then continues the sending instruction */
	sendMessageLoading( message: any) {
		this.setState({ isLoading: true });
		this.props.sendMessage( message);
		setTimeout(() => {
			this.setState({ isLoading: false });
		}, 400);
	}
	render() {
		return (
			<div className={"chatApp__conv"}>
				<Title
					owner={this.props.senderUsername}
					backButton={this.props.backButton}
				/>
				<MessageList
					owner={this.props.owner}
					ownerAvatar={this.props.ownerAvatar}
					messages={this.props.messages}
					senderAvatar= {this.props.senderAvatar}
				/>
				<div className={"chatApp__convSendMessage clearfix"}>
					<InputMessage
						isLoading={this.state.isLoading}
						owner={this.props.owner}
						ownerAvatar={this.props.ownerAvatar}
						sendMessage={this.props.sendMessage}
						sendMessageLoading={this.sendMessageLoading}
						senderUsername={this.props.senderUsername}
					/>
				</div>
			</div>
		);
	}
}
/* end ChatBox component */
/* ========== */

