import gql from 'graphql-tag'

export const getUser = gql`
  query GetUser($id: ID!) {
    getUser(id: $id) {
      id
      username
      county
      avatar
      cardInfo
      active
      tickets {
        items {
          ticket{
          id
          content
          time
          destination
          price
          title
          county
          source
          status
          ticketEmployeeId
          ticketUserId
          ticketType
          closedComment
          closedTime
          ownerUploadImages
          employeeUploadImages
          execptedPrice
          users {
            items {
              user{
              id
              }
            }
          }
        }
        }
      }
    }
  }
`

