export const getConvo = gql`
  query GetConvo($id: ID!, $nextToken: String) {
    getConvo(id: $id) {
      id
      messages(sortDirection: DESC, limit: 20, nextToken: $nextToken) {
        nextToken
        items {
          id
          content
          createdAt
          owner
          chatbot
          isSent
          file
          messageConversationId
        }
      }
    }
  }
`

export const searchMessages = gql`
  query SearchMessages(
    $filter: SearchableMessageFilterInput
    $sort: SearchableMessageSortInput
    $limit: Int
    $nextToken: Int
  ) {
    searchMessages(
      filter: $filter
      sort: $sort
      limit: $limit
      nextToken: $nextToken
    ) {
      items {
        id
        content
        createdAt
        owner
        chatbot
        isSent
        file {
          bucket
          region
          key
        }
        messageConversationId
        conversation {
          id
          name
          createdAt
        }
      }
      nextToken
    }
  }
`
export const searchUsers = gql`
  query SearchUsers(
    $filter: SearchableUserFilterInput
    $sort: SearchableUserSortInput
    $limit: Int
    $nextToken: Int
  ) {
    searchUsers(
      filter: $filter
      sort: $sort
      limit: $limit
      nextToken: $nextToken
    ) {
      items {
        id
        username
        registered
        userConversations {
          items {
            id
            name
            status
            convoLinkUserId
          }
          nextToken
        }
      }
      nextToken
    }
  }
`
export const searchConvoLinks = gql`
  query SearchConvoLinks(
    $filter: SearchableConvoLinkFilterInput
    $sort: SearchableConvoLinkSortInput
    $limit: Int
    $nextToken: Int
  ) {
    searchConvoLinks(
      filter: $filter
      sort: $sort
      limit: $limit
      nextToken: $nextToken
    ) {
      items {
        id
        name
        status
        convoLinkUserId
        user {
          id
          username
          registered
        }
        conversation {
          id
          name
          createdAt
        }
      }
      nextToken
    }
  }
`

export const getOpenTicket = gql`
query getOpenTicket($status: String!, $county: String! ) {
  jobsByStatus(county: $county,  status: {
    eq: $status
  }) 
   {
      items {
        id
        content
        time
        destination
        price
        title
        county
        source
        status
        ownerUploadImages
        employeeUploadImages
        execptedPrice
        ticketType
      }
    nextToken
   }
}
`

export const getUserMessages = gql`
query messageByReceiver($receiver: String!, $isSent: String! ) {
  messageByReceiver(receiver: $receiver,  isSent: {
    eq: $isSent
  }) 
   {
      items {
        id
        isSent
        owner
        conversation {
          id
        }
      }
    nextToken
   }
}
`


export const getUserConversations = gql`
  query GetUser($id: ID!) {
    getUser(id: $id) {
      id
      username
      registered
      avatar
      userConversations {
        items {
          id
          name
          status
          conversation {
            id
            name
            createdAt
            associated {
              items {
                convoLinkUserId
                user {
                  id
                  username
                  avatar
                }
              }
            }
          }
        }
      }
    }
  }
`


