import React from 'react';
import Dialog, { DialogProps } from '@material-ui/core/Dialog';
//import DialogTitle from '@material-ui/core/DialogTitle';
import { AuthContext } from '../auth/auth-context'
import { ImageGroup, Image } from 'react-fullscreen-image'
import { DialogTitle, getImages, Employee } from '../utils';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import './imageView.css';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import {  getFullUser } from '../graphql/queries'
import { API, graphqlOperation } from 'aws-amplify'

interface AcceptProps {
  openPop: boolean;
  closeAlert: (op: boolean) => void;
  employee: string;

}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
        padding: 15
    }
})
);

const ExpertViewer: React.SFC<AcceptProps> = ({employee, openPop, closeAlert}) => {
  const [scroll, setScroll] = React.useState<DialogProps['scroll']>('paper');
  const authContext = React.useContext(AuthContext)
  const [images, setImages] = React.useState<string[]>([]);
  const classes = useStyles();
  const [emp, setEmp] = React.useState<Employee>();

  React.useEffect(() => {
    const fetchData = async (phone: string) => {

      const empDate = await API.graphql(graphqlOperation(getFullUser, { id: employee }))
      //@ts-ignore
      if(empDate.data.getUser){
      //@ts-ignore
       let emp =  empDate.data.getUser;

        const address = JSON.parse(emp.address);
        setEmp({
            id : emp.id,
            avatar : "",
            address: "",
            aboutme: address.aboutme,
            phoneNumber: address.phoneNumber,
            username : `${address.firstName} ${address.lastName}`
        })
        if(address.pictures){
            const files = JSON.parse(address.pictures);
            const result = await getImages(files);
            //@ts-ignore
            setImages(result);
        }

      }

    };
    const id = authContext.userId;
    if(id){
       const phone =  authContext.user.attributes["phone_number"];
        fetchData(phone);
    }
  }, [authContext.userId]);


  React.useEffect(() => {
    if(openPop){
      setScroll("paper");
    }
  }, [openPop]);


  const handleClose = () => {
    closeAlert(false);
  };

  const descriptionElementRef = React.useRef<HTMLElement>(null);
  React.useEffect(() => {
    if (openPop) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [openPop]);

  //const mm = "https://homepages.cae.wisc.edu/~ece533/images/airplane.png";

  return ( emp ?
      <Dialog
        open={openPop}
        onClose={handleClose}
        scroll={scroll}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle id="scroll-dialog-title" onClose={handleClose} >Service Provider Details</DialogTitle>
        <div className={classes.container}>

        <Grid container spacing={1}>
        <Grid item xs={12} sm={6}>
          <TextField
           disabled = {true}
            required
            id="username"
            name="username"
            label="UserName"
            fullWidth
            defaultValue={emp.username}
            autoComplete="username"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
                    disabled = {true}

            required
            id="aboutme"
            name="aboutme"
            label="About Me"
            fullWidth
            defaultValue={emp.aboutme}
            autoComplete="aboutme"
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <TextField
             disabled = {true}

            required
            id="phoneNumber"
            name="phoneNumber"
            label="Phone Number"
            fullWidth
            defaultValue={emp.phoneNumber}
            autoComplete="phoneNumber"
          />
        </Grid>

        </Grid>

           <ImageGroup>
        <ul className="images">
          {images.map((currElement, index)=> (
            <li key={index}>
              <Image src={currElement} alt="mountains" />
            </li>
          ))}
        </ul>
      </ImageGroup>
      </div>
      </Dialog> : null
  );
}

export default ExpertViewer;