import React from 'react';
import {Elements} from '@stripe/react-stripe-js';
import {loadStripe} from '@stripe/stripe-js';

import CheckoutForm from './CardSetupForm';

// Make sure to call `loadStripe` outside of a components render to avoid
// recreating the `Stripe` object on every render.
const stripePromise = loadStripe("pk_test_n28At7VrrTS32nAdaH5oyTo100BjZFd5oo");

export interface PaymentFormProps {
    payment: string;
    nextButton: (value: string) => void
    backButton: () => void,
    address: string;
  }

  
const Stripe: React.SFC<PaymentFormProps> = (props) => {

  return (
    <Elements stripe={stripePromise}>
      <CheckoutForm address={props.address} payment={props.payment} nextButton= {props.nextButton} backButton= {props.backButton}/>
    </Elements>
  );
}

export default  Stripe;