
  import {Employee} from '../utils'
  
  export interface Ticket {
  id: string
  content: string
  createdAt: string
  owner: string
  ticketUserId: string
  ticketEmployeeId: string
  completed: boolean
  price: string
  file: any
  title: string
  county: string
  source: string
  destination: string
  time: string
  status: string
  closedComment: string
  closedTime: string
  ownerUploadImages: string
  employeeUploadImages: string
  employee: Employee,
  daytime: string,
  ticketType: string,
  execptedPrice : string
  }
  export interface bidserviceProvider {
    serviceProvider: string,
    price: string,
    username ?: string,
    email?: string,
    phonenumber?: string
  }

  export const isServiceProviderBid = ( id: string, ticket: Ticket) => {
    const bidExperts = ticket.execptedPrice ? JSON.parse(ticket.execptedPrice) : []
    const currentBid = bidExperts.filter(function(el: any) { return (el.serviceProvider === id) })
    return currentBid.length > 0 ? false : true 
  }

  export const getServiceProviderBid = ( id: string, ticket: Ticket): bidserviceProvider| undefined => {
    const bidExperts = ticket.execptedPrice ? JSON.parse(ticket.execptedPrice) : []
    const currentBid = bidExperts.filter(function(el: any) { return (el.serviceProvider === id) })
    return currentBid.length > 0 ? currentBid[0] : undefined
  }