import React, { useEffect, useCallback, useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Typography from '@material-ui/core/Typography';
import AddressForm from './AddressForm';
import EmployeeBackgroundCheck from './EmployeeBackgroundCheck';
import AppHomeLayout from '../app/AppHomeLayout'
import { registerUser, updateUser} from '../graphql/mutations'
import { API, graphqlOperation } from 'aws-amplify'
import {  getFullUser } from '../graphql/queries'
import { useHistory } from 'react-router-dom'
import { AuthContext } from '../auth/auth-context'
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Stripe from './Stripe/Stripe';

import Snackbar from '../common/Snackbar'

const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative',
  },
  layout: {
    width: 'auto',
    //marginLeft: theme.spacing(2),
    //marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 600,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    padding: theme.spacing(3, 0, 5),
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

export interface CheckoutProps {}

const Checkout: React.SFC<CheckoutProps> = () => {
    const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [address, setAddress] = React.useState("");
  const [payment, setPayment] = React.useState("");
  const [error, setError] = useState<string>('')
  const [isCreate, setisCreate] = useState<boolean>(true)
  const history = useHistory()
  const authContext = React.useContext(AuthContext)
  const [isEmp, setisEmp] = useState<boolean>(false)
  const [backdrop, setBackdrop] = React.useState(false);

  const steps = ['Your Information', 'Payment details'];

  const navigator_utl = () =>{
    navigator.geolocation.getCurrentPosition(
      position => {
        if(Object.keys(position).length > 0){
          // setlatitudes(position.coords.latitude.toString());
          // setlongitudes(position.coords.longitude.toString());
        }else {
          setError("longitude and latitude missing")
        }
      },
      error => {
        setError(error.message)
      },
      { enableHighAccuracy: true, timeout: 20000, maximumAge: 1000 }
    );
  }

// useEffect(() => {
//   navigator_utl();
// }, []);


useEffect(() => {
  if(authContext && authContext.userId && authContext.user ){

    const type = authContext.user.attributes["custom:account_type"];
    type ==="Customer" ? setisEmp(false) : setisEmp(true)
  
    const op =  getFullUser//type ==="Customer" ? getFullUser : getFullEmployee;    

    const id = authContext.userId;
  //"37ff912b-27ac-4e5d-87e5-a4edaad80213";
  (async function anyNameFunction() {
    const commentData = await API.graphql(graphqlOperation(op, { id }))
    console.log('fetchDate',commentData); 

    //@ts-ignore
    if(commentData.data.getUser){
      setisCreate(false);
    //@ts-ignore
    setAddress(commentData.data.getUser.address)
        //@ts-ignore
    setPayment(commentData.data.getUser.cardInfo)
    }

   })();
  }
  
}, [authContext.userId]);



  const updateProfile = async(paymentV: string) =>{

    setBackdrop(true)
    const id = authContext.userId;
    const type = authContext.user.attributes["custom:account_type"];

    const op =  type ==="Customer" ? (isCreate?  registerUser: updateUser) :
       (isCreate?  registerUser: updateUser)
       
     // isCreate ? registerUser : updateUser
    const response =  await API.graphql(
      graphqlOperation(op, { input: {
        id : id,
        username : JSON.parse(address).firstName,
        registered : "yes",
        active : type ==="Customer" ? true :false,
        usertype:  type ==="Customer" ? "Customer" :"Employee",
        address : address,
        zipcode : JSON.parse(address).zip,
        latitudes: JSON.parse(address).latitudes,
        longitudes :  JSON.parse(address).longitudes,
        aboutme: JSON.parse(address).aboutme,
        avatar: JSON.parse(address).pictures,
        cardInfo :  paymentV,
        county: JSON.parse(address).county
      } })
  );
  console.log("response", response);
  setBackdrop(false);

  history.push('/');


  }

const nextButton = async(value: string) => {
  console.log("nextButton", value);
  if(activeStep == 0){
    setAddress(value);
  }else if(activeStep == 1)
  {
    setPayment(value);
    updateProfile(value);
    return;
  }
  handleNext();
}

 function getStepContent(step: any) {

  switch (step) {
    case 0:
      return <AddressForm address={address} nextButton= {nextButton} />;
    case 1:
       if(isEmp){
         return <EmployeeBackgroundCheck payment={payment} nextButton= {nextButton} backButton= {handleBack} />;
       }else {
           return <Stripe address={address} payment={payment} nextButton= {nextButton} backButton= {handleBack} />
       }   
    // case 2:
    //   return <Review address={address} payment={payment} nextButton= {updateProfile} backButton= {handleBack}/>;
    default:
      throw new Error('Unknown step');
  }

}


  const handleNext = () => {
    setActiveStep(activeStep + 1);
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  const setErrorMsg = useCallback((message: string) => {
    setError(message)
  }, [])

  return (
    <React.Fragment>
    <AppHomeLayout title='Profile List' header = "profile">

      <CssBaseline />
      <main className={classes.layout}>
        <Paper className={classes.paper}>
          <Stepper activeStep={activeStep} className={classes.stepper}>
            {steps.map(label => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <React.Fragment>
            {activeStep === steps.length ? (
              <React.Fragment>
                <Typography variant="h5" gutterBottom>
                  Need Update BackEnd.
                </Typography>
              </React.Fragment>
            ) : (
              <React.Fragment>
                {getStepContent(activeStep)}
              </React.Fragment>
            )}
          </React.Fragment>
        </Paper>
      </main>
      </AppHomeLayout>
      <Snackbar
          variant='error'
          message={error}
          setMessage={message => setErrorMsg(message)}
        />
      <Backdrop className={classes.backdrop} open={backdrop}>
        <CircularProgress color="inherit" />
      </Backdrop>
    </React.Fragment>
  );
}

export default Checkout;
