import React from 'react';
import Dialog, { DialogProps } from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';
import DialogActions from '@material-ui/core/DialogActions';
//import DialogTitle from '@material-ui/core/DialogTitle';
import { AuthContext } from '../auth/auth-context'
import { DialogTitle, getImages, Employee } from '../utils';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import './imageView.css';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';

interface AcceptProps {
  openPop: boolean;
  closeAlert: (op: boolean, execptedPrice: string, isNew: boolean) => void;
  execptedPrice: string;
  id : string,
  user: any
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
        padding: 15
    }
})
);

const BidDialog: React.SFC<AcceptProps> = ({execptedPrice, user, openPop, id, closeAlert}) => {
  const [scroll, setScroll] = React.useState<DialogProps['scroll']>('paper');
  const classes = useStyles();
  const [price, setPrice] = React.useState<string>("");
  const [newPrice, setNewPrice] = React.useState<string>("");

  React.useEffect(() => {

   const bidExperts = execptedPrice ? JSON.parse(execptedPrice) : []
   const currentBid = bidExperts.filter(function(el: any) { return (el.serviceProvider === id) })
   setPrice(currentBid.length > 0 ? currentBid[0].price : "" );

  }, [execptedPrice, id]);

  React.useEffect(() => {
    if(openPop){
      setScroll("paper");
    }
  }, [openPop]);

  const handleInputChange = (e: any) => {
    const {value} = e.target
    setNewPrice(value)
}

  const handleClose = () => {
      if(newPrice.length >0){
        let items = []
        // price exist means its updating bid
        if(price.length > 0){
            const bidExperts = JSON.parse(execptedPrice)

            bidExperts.forEach((element: any, index: number) => {
                if(element.serviceProvider === id) {
                    bidExperts[index].price = newPrice;
                }
            });
            console.log("bidExperts",bidExperts);
            closeAlert(true, JSON.stringify(bidExperts), false);

        }else {
            items.push({
                serviceProvider: id,
                price: newPrice,
                username : user?.username
              })
            console.log("bidExperts",items);
            closeAlert(true, JSON.stringify(items), true);
        }
      }else {
        closeAlert(false, "", false);
      }
  };

  const descriptionElementRef = React.useRef<HTMLElement>(null);
  React.useEffect(() => {
    if (openPop) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [openPop]);

  //const mm = "https://homepages.cae.wisc.edu/~ece533/images/airplane.png";

  return ( 
      <Dialog
        open={openPop}
        onClose={handleClose}
        scroll={scroll}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle id="scroll-dialog-title" onClose={handleClose} >Service Provider Details</DialogTitle>
        <div className={classes.container}>

        <Grid container spacing={1}>
        {price && price.length > 0 ?
        <Grid item xs={12} >
          <TextField
           disabled = {true}
            required
            id="price"
            name="price"
            label="old price"
            fullWidth
            defaultValue={price}
            autoComplete="old price"
          />
        </Grid>: null}
        <Grid item xs={12}>
          <TextField
            required
            id="newprice"
            name="newprice"
            label={price && price.length > 0 ?"Latest price": "Enter your Bid amount"} 
            fullWidth
            value={newPrice}
            onChange={handleInputChange}
            autoComplete="Bid Amount"
          />
        </Grid>

        </Grid>

      </div>

      <DialogActions>
          <Button onClick={handleClose} variant="contained" color="primary" autoFocus>
            Bid
          </Button>
        </DialogActions>

      </Dialog>
  );
}

export default BidDialog;