import React, { useState, useEffect, } from 'react'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import {makeStyles}  from '@material-ui/core/styles';
import { GeocoderResult} from './model';
import LocationSearchInput from './LocationSearchInput';
import {stripAddress} from './util';
import ImageUploader from 'react-images-upload';
import {handleImageUpload, getImages, TVBackdrop} from '../utils';
import Snackbar from '../common/Snackbar'
import { AuthContext } from '../auth/auth-context'

const useStyles = makeStyles(theme => ({
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
}));

export interface AddressFormProps {
  address: string;
  nextButton: (value: string) => void
}

  const AddressForm: React.SFC<AddressFormProps> = (props) => {
    const authContext = React.useContext(AuthContext)

    const classes = useStyles();

  const handleInputChange = (e: any) => {
    const {name, value} = e.target
    setValues({...values, [name]: value})
}

const handleNext = async() => {
  const phone_number = authContext.user.attributes["phone_number"];
  const email = authContext.user.attributes["email"];

  if( props.address){
    const address = JSON.parse(props.address)
    const valuessss = {...values, pictures: address.pictures, phoneNumber: phone_number, email: email}
    props.nextButton(JSON.stringify(valuessss));

  }else {
    setLoaded(true)

    console.log("value",values, pictures);
    if(pictures.length >0){
     const S3path = await handleImageUpload(pictures); 
     console.log("S3path",S3path);
     const valuessss = {...values, pictures: JSON.stringify(S3path),
      phoneNumber: phone_number, email: email
    }
     props.nextButton(JSON.stringify(valuessss));
    }else {
     // props.nextButton(JSON.stringify(values));
     // setLoaded(false)
     setError("picture missing");
    }
    setLoaded(false)
  }
}

React.useEffect(() => {

  const fetchData = async (inputimages: string) => {
    const address = JSON.parse(inputimages)

    let picture = null;
    if(address.pictures){
      const files = JSON.parse(address.pictures);
      picture = await getImages(files);
      console.log("images", picture);
    }
   
    setValues({...address, pictures: picture});
    setLoaded(false)
  };

  if(props.address){
    setLoaded(true)
    fetchData(props.address);
  } 

}, [props.address]);


 const [values, setValues] = useState({firstName: '', lastName: '',middelName: '',address1: '', address2: '',
 city: '', state: '',zip: '', country: '',county: '', latitudes: '', longitudes: '', aboutme: "", pictures: ""})

  const [disable, setDisable] = useState<boolean>(true)

  useEffect(() => {
    const {firstName, lastName, address1, city, zip, country, aboutme} = values
    setDisable(!(firstName &&  lastName && address1 &&  city &&  zip &&  country && aboutme))
  }, [values ])

  const resultGoogle = (result: GeocoderResult[]) => {
    console.log("address",result);
    const value = stripAddress(result);
    setValues({...values, ...value})
  };

  // image upload
  const [pictures, setPictures] = useState<any[]>([]);

  const onDrop = async (picture: any) => {
    setPictures(picture);
  };

  // onload
  const [loaded, setLoaded] = useState<boolean>(false);

  //error
  const [error, setError] = useState<string>('')
  const setErrorMsg = React.useCallback((message: string) => {
    setError(message)
  }, [])

  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
      Your Information
      </Typography>
      <Grid container spacing={1}>
      <Grid item xs={12} sm={6}>
          <TextField
            required
            id="firstName"
            name="firstName"
            label="First name"
            fullWidth
            onChange={handleInputChange}
            value={values.firstName}
            autoComplete="fname"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="middelName"
            name="middelName"
            label="Middel Name"
            fullWidth
            onChange={handleInputChange}
            value={values.middelName}
            autoComplete="middelName"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="lastName"
            name="lastName"
            label="Last name"
            fullWidth
            onChange={handleInputChange}
            value={values.lastName}
            autoComplete="lname"
          />
        </Grid>

      {
         values.address1.length < 2 ? ( 
          <Grid item xs={12} sm={6}>
            <LocationSearchInput label= "Enter your Home address" googleAddressReturn={resultGoogle}
         />
          </Grid>
        ):
        <Grid container spacing={1}>
        <Grid item xs={12}>
          <TextField
            required
            id="address1"
            name="address1"
            label="Address line 1"
            fullWidth
            onChange={handleInputChange}
            value={values.address1}
            autoComplete="billing address-line1"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            id="address2"
            name="address2"
            label="Address line 2"
            fullWidth
            onChange={handleInputChange}
            value={values.address2}
            autoComplete="billing address-line2"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="city"
            name="city"
            label="City"
            fullWidth
            onChange={handleInputChange}
            value={values.city}
            autoComplete="billing address-level2"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField id="state" name="state" label="State/Province/Region"
                      onChange={handleInputChange}
                      value={values.state}
          fullWidth />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="zip"
            name="zip"
            label="Zip / Postal code"
            fullWidth
            onChange={handleInputChange}
            value={values.zip}
            autoComplete="billing postal-code"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="country"
            name="country"
            label="Country"
            fullWidth
            onChange={handleInputChange}
            value={values.country}
            autoComplete="billing country"
          />
        </Grid>

        <Grid item xs={12}>
        <TextField
          multiline
          rows="6"
          required
          id="aboutme"
          name="aboutme"
          label="About Me"
          fullWidth
          onChange={handleInputChange}
          value={values.aboutme}
          autoComplete="content"
        />
        </Grid>

        <Grid item xs={12}>

{
  values.pictures ?
  <img
  style={{width:'300px', height:'300px'}}
  src={ values.pictures}
/>
  
  :
        <ImageUploader
      withPreview={true}
      withIcon={true}
      onChange={onDrop}
      singleImage = {true}
      imgExtension={[".jpg", ".gif", ".png", ".jpeg"]}
      maxFileSize={5242880}
    />
           
}
</Grid>

        
         </Grid>
        }

        <Grid
  container
  direction="row"
  justify="flex-end"
  alignItems="center"
>

<Button disabled= {disable} variant="contained"
  color="primary"
  className={classes.button}
onClick={handleNext}>next</Button>

</Grid>

<TVBackdrop open={loaded}/>
<Snackbar
          variant='error'
          message={error}
          setMessage={message => setErrorMsg(message)}
        />
      </Grid>
    </React.Fragment>
  );
}

export default AddressForm
