import gql from 'graphql-tag'

export const onCreateMessage = gql`
  subscription OnCreateMessage($messageConversationId: ID!) {
    onCreateMessage(messageConversationId: $messageConversationId) {
      id
      content
      createdAt
      owner
      chatbot
      isSent
      file
      messageConversationId
      conversation {
        id
        name
        createdAt
      }
    }
  }
`

export const onCreateTicket = gql`
  subscription OnCreateTicket($county: String!) {
    onCreateTicket(county: $county) {
      id
      countAndId
      ticketUserId
      owner
      content
      time
      destination
      price
      title
      county
      source
      status
      ticketEmployeeInfo
      ticketEmployeeId
      ticketUserInfo
      ownerUploadImages
      employeeUploadImages
      closedComment
      ticketType
      execptedPrice
    }
  }
`

export const onUpdatedTicket = gql`
  subscription OnUpdatedTicket($county: String!) {
    onUpdatedTicket(county: $county) {
      id
      content
      time
      destination
      ticketType
      price
      title
      county
      source
      status
      ticketEmployeeId
      closedComment
      closedTime
      execptedPrice
    }
  }
`

export const onUpdateConvoLink = gql`
  subscription OnUpdateConvoLink($convoLinkUserId: ID, $status: String) {
    onUpdateConvoLink(convoLinkUserId: $convoLinkUserId, status: $status) {
      id
      name
      status
      conversation {
        id
        name
        createdAt
        associated {
          items {
            convoLinkUserId
            user {
              id
              username
            }
          }
        }
      }
    }
  }
`
