import { API} from 'aws-amplify'

export const  createCustomer=  async(email: string, phoneNumber: string) =>{
    let myInit = { 
        response: true,
        body: {
            email: email,
            phone: phoneNumber
        },
        headers: {} // OPTIONAL
    }

    const response =  await API.post("stripeapi", "/stripe/createcustomer",myInit);
    console.log("setupIntents", response);

    return {
        customerId: response.data.url.id,
        customerKey: response.data.intent.client_secret
    }
}

