
import React from 'react';
import {useStripe, useElements} from '@stripe/react-stripe-js';
import {
    CardNumberElement,
    CardCvcElement,
    CardExpiryElement
  } from "@stripe/react-stripe-js";
import Grid from '@material-ui/core/Grid';
import {makeStyles}  from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { createCustomer} from '../../utils/Stripe';
import { AuthContext } from '../../auth/auth-context'
import TextField from '@material-ui/core/TextField';
import Snackbar from '../../common/Snackbar'
import {TVBackdrop} from '../../utils';

import './SplitForm.css'


const useOptions = () => {
    const fontSize = "16px";
    const options = React.useMemo(
      () => ({
        style: {
          base: {
            fontSize,
            color: "#424770",
            letterSpacing: "0.025em",
            fontFamily: "Source Code Pro, monospace",
            "::placeholder": {
              color: "#aab7c4"
            }
          },
          invalid: {
            color: "#9e2146"
          }
        }
      }),
      [fontSize]
    );
  
    return options;
  };

  const useStyles = makeStyles(theme => ({
    buttons: {
      display: 'flex',
      justifyContent: 'flex-end',
    },
    button: {
      marginTop: theme.spacing(3),
      marginLeft: theme.spacing(1),
    },
  }));

  
  export interface CardSetupFormProps {
    payment: string;
    nextButton: (value: string) => void
    backButton: () => void,
    address: string;
  }
  
const CardSetupForm: React.SFC<CardSetupFormProps> = (props) => {
  const stripe = useStripe();
  const elements = useElements();
  const classes = useStyles();
  const authContext = React.useContext(AuthContext)
  const [loaded, setLoaded] = React.useState<boolean>(false);

  React.useEffect(() => {
    //if(props.payment) setValues(JSON.parse(props.payment))
  }, [props.payment]);

  const handleSubmit = async (event: any) => {

    // We don't want to let default form submission happen here,
    // which would refresh the page.
    event.preventDefault();

    if (!stripe || !elements) {
      // Stripe.js has not yet loaded.
      // Make sure to disable form submission until Stripe.js has loaded.
      return;
    }

    if (values.name.length < 2) {
        setError("Enter Name")

        return;
      }
      setLoaded(true)

    const phone = authContext.user.attributes["phone_number"];
    const email = authContext.user.attributes["email"];

    const keys = await createCustomer(email, phone);

    //@ts-ignore
    const result = await stripe.confirmCardSetup(keys.customerKey, {
      payment_method: {
           //@ts-ignore
           card: elements.getElement(CardNumberElement),
           billing_details: {
          name: values.name,
        },
      }
    });

    if (result.error) {
        setError(result.error.message)

        console.log("esult.setupIntent.payment_method", result);

      // Display result.error.message in your UI.
    } else {
        console.log("esult.setupIntent.payment_method", result.setupIntent);
      // The setup has succeeded. Display a success message and send
      // result.setupIntent.payment_method to your server to save the
      // card to a Customer
      setLoaded(false)

      props.nextButton(JSON.stringify({
        ...keys,
        name:values.name
       // card: elements.getElement(CardNumberElement)
    }));

    }

 
  };
  const options = useOptions();
  const [error, setError] = React.useState<string>('')
  const [values, setValues] = React.useState({cardNumber: '', expiry: '',cvc: '', name: ''})
  const setErrorMsg = React.useCallback((message: string) => {
    setError(message)
  }, [])
  
  return (
         <form onSubmit={handleSubmit}>
             { !props.payment
          ? 
            <Grid container spacing={1}>

            <Grid item xs={12} sm={6}>
          <TextField
            required
            id="firstName"
            name="firstName"
            label="First name"
            fullWidth
            onChange={(e: any) => {
                const {value} = e.target
                setValues({...values, name: value});
              }}            
            value={values.name}
            autoComplete="fname"
          />
        </Grid>
        <Grid item xs={12} sm={6}>

              <label>
        Card number
        <CardNumberElement
          options={options}
          onReady={() => {
            console.log("CardNumberElement [ready]");
          }}
          onChange={event => {
            // const {value} = event.target;
            // setValues({...values, name: value});
            console.log("CardNumberElement [change]", event);
          }}
          onBlur={() => {
            console.log("CardNumberElement [blur]");
          }}
          onFocus={() => {
            console.log("CardNumberElement [focus]");
          }}
        />
      </label>
      </Grid>
      <Grid item xs={12} sm={6}>

      <label>
        Expiration date
        <CardExpiryElement
          options={options}
          onReady={() => {
            console.log("CardNumberElement [ready]");
          }}
          onChange={event => {
            console.log("CardNumberElement [change]", event);
          }}
          onBlur={() => {
            console.log("CardNumberElement [blur]");
          }}
          onFocus={() => {
            console.log("CardNumberElement [focus]");
          }}
        />
      </label>
      </Grid>

      <Grid item xs={12} sm={6}>

      <label>
        CVC
        <CardCvcElement
          options={options}
          onReady={() => {
            console.log("CardNumberElement [ready]");
          }}
          onChange={event => {
            console.log("CardNumberElement [change]", event);
          }}
          onBlur={() => {
            console.log("CardNumberElement [blur]");
          }}
          onFocus={() => {
            console.log("CardNumberElement [focus]");
          }}
        />
      </label>
      </Grid>

      <Grid
  container
  direction="row"
  justify="flex-end"
  alignItems="center"
>
        <div className={classes.buttons}>
                <Button 
                 variant="contained"
                onClick={props.backButton} className={classes.button}>
                      Back
                    </Button>
                  <Button
                  disabled={!stripe}
                    variant="contained"
                    color="primary"
                    onClick={handleSubmit}
                    className={classes.button}
                  >
                   Save
                  </Button>
                   </div>
              </Grid>

              <Snackbar
          variant='error'
          message={error}
          setMessage={message => setErrorMsg(message)}
        />
        </Grid>
: 
<Grid
container
direction="row"
justify="center"
alignItems="center"
>
<div> Currently, you will not be able to change your payment details here. Contact us at contact@travtail.com to request for changes to your payment details.</div>
</Grid>
}
<TVBackdrop open={loaded}/>

    </form>   
  );
}

export default  CardSetupForm;