import React, { useState, useEffect, useContext, useCallback } from 'react'
import { useHistory } from 'react-router-dom'
import { Link, Grid } from '@material-ui/core'
import Layout from '../app/AppLayout'
import Snackbar from '../common/Snackbar'

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

import { AuthContext } from './auth-context'
import AuthLayout from './components/AuthLayout'
import AuthEmailField from './components/AuthEmailField'
import AuthReEnterPasswordField from './components/AuthReEnterPasswordField'
import AuthPhoneField from './components/AuthPhoneField'
import AuthPasswordField from './components/AuthPasswordField'
import AuthButton from './components/AuthButton'
import { useStyles } from './components/styles'

export interface AuthSignUpProps {}

const AuthSignUp: React.SFC<AuthSignUpProps> = () => {
  const authContext = useContext(AuthContext)
  const [email, setEmail] = useState<string>('')
  const [password, setPassword] = useState<string>('')
  const [rePassword, setRePassword] = useState<string>('')
  const [phoneNumber, setPhoneNumber] = useState<string>('')

  const [disable, setDisable] = useState<boolean>(true)
  const [error, setError] = useState<string>('')
  const history = useHistory()


  const [radio, setRadio] = React.useState('');

  const handleRadioChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRadio((event.target as HTMLInputElement).value);
  };

  useEffect(() => {
    setDisable(!(email && password && phoneNumber && radio))
    if(password !== rePassword)
{
  setDisable(true)
}
  }, [email, password, rePassword, phoneNumber, radio])

  const submitHandler = (e: any) => {
    e.preventDefault();
    const formattedPhone = phoneNumber.replace( /^\D+/g, '');
    authContext
      .signUp(email, password, formattedPhone, radio)
      .then(data => {
        history.push('/auth/signup/confirm', formattedPhone)
      })
      .catch(err => {
        console.error('error:', err)
        setError(err)
      })
  }

  const setErrorMsg = useCallback((message: string) => {
    setError(message)
  }, [])

  const classes = useStyles()
  return (
    <Layout title='Sign Up'>
      <AuthLayout title='Sign Up'>
        <Snackbar
          variant='error'
          message={error}
          setMessage={message => setErrorMsg(message)}
        />
        <form
          className={classes.form}
          onSubmit={e => submitHandler(e)}
          noValidate>
          <AuthPhoneField setPhoneNumber={phoneNumber => setPhoneNumber(phoneNumber)} />
          <AuthEmailField setEmail={email => setEmail(email)} />
          <AuthPasswordField setPassword={password => setPassword(password)} />
          <AuthReEnterPasswordField authpass={password} setRePassword={rePassword => setRePassword(rePassword)} />


          <FormControl  className={classes.form}  component="fieldset">
      <FormLabel component="legend">Account Type</FormLabel>
      <RadioGroup row aria-label="position"  value={radio} onChange={handleRadioChange}>
      <FormControlLabel value="Customer" control={<Radio />} label="Customer" />
          <FormControlLabel value="Employee" control={<Radio />} label="Service Provider" />
      </RadioGroup>
    </FormControl>


          <AuthButton disabled={disable}>Sign Up</AuthButton>
          <Grid container>
            <Grid item xs className={classes.links}>
              <Link
                href='#'
                onClick={() => history.push('/auth')}
                variant='body2'>
                {'Sign In'}
              </Link>
            </Grid>
            <Grid item className={classes.links}>
              <Link
                href='#'
                onClick={() => history.push('/auth/signup/confirmemail')}
                variant='body2'>
                {'Confirm Sign Up'}
              </Link>
            </Grid>
          </Grid>
        </form>
      </AuthLayout>
    </Layout>
  )
}

export default AuthSignUp
