import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog, { DialogProps } from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import {Ticket, bidserviceProvider, getServiceProviderBid} from './model';
import { assignExpertToTicket, updateTicket, createUserTicket } from '../graphql/mutations'
import { API, graphqlOperation} from 'aws-amplify'
import { AuthContext } from '../auth/auth-context'
import LocationSearchInput from '../profilepage/LocationSearchInput';
import {stripAddress} from '../profilepage/util';
import { GeocoderResult} from '../profilepage/model';
import { DialogTitle } from '../utils/DialogTitle';
import AlertDialog from './AlertDialog';
import ImageViewer from '../utils/ImageViewer';
import Chip from '@material-ui/core/Chip';
import FaceIcon from '@material-ui/icons/Face';
import DoneIcon from '@material-ui/icons/Done';
import AttachmentIcon from '@material-ui/icons/Attachment';
import ExpertViewer from '../utils/ExpertViewer';
import ChatIcon from '@material-ui/icons/Chat';
import { startConvoWithUser} from './util';
import { useHistory } from 'react-router-dom'
// @ts-ignore
import { uuid } from 'uuidv4';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import Typography from '@material-ui/core/Typography';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import BidDialog from '../utils/BidDialog';

interface AcceptProps {
  openPop: boolean;
  close: (op: boolean, updateTicket: Ticket) => void;
  data: Ticket;
  setIsEmployee : boolean;
  user: any
}


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
      //maxWidth: 400,
      backgroundColor: theme.palette.background.paper,
    },
    inline: {
      display: 'inline',
    },
    subHeader: {
      color: "rgba(0, 0, 0, 0.98)",
      fontSize: 18,
      fontWeight: 900,
  },
    button: {
       width : 20,
       marginTop: 15,
       padding: 5,
       margin: theme.spacing(1),
    },
    rating: {
        marginLeft: 15
    },
    buttonCart: {
        marginLeft: 5,
        marginTop: 15,
        width: 10
    },
    buttonGird: {
        width: 90
    },
    cartLabel: {
        width: 10,
        marginBottom: 8,
        marginLeft: 5
    }
  }),
);

const AcceptTicket: React.SFC<AcceptProps> = ({data,user, openPop, close, setIsEmployee}) => {
  const [scroll, setScroll] = React.useState<DialogProps['scroll']>('paper');
  const authContext = React.useContext(AuthContext)
  const [formEdit, setFormEdit] = React.useState(true);
  const history = useHistory()
  const classes = useStyles();

  React.useEffect(() => {
    if(openPop){
      setScroll("paper");
    }
  }, [openPop]);

  const handleInputChange = (e: any) => {
    const {name, value} = e.target
    setValues({...values, [name]: value})
}

React.useEffect(() => {
  if(data.id){
    setValues(data)
    if(data.execptedPrice){
      setBids(JSON.parse(data.execptedPrice))
    }
  }
  setclosedComment(data.closedComment)
}, [data]);

const [bids, setBids] = React.useState<bidserviceProvider[]>([]);

const [closedComment, setclosedComment] = React.useState("");

const [values, setValues] = React.useState({ content: '',price: '', title: '',
source: '', destination: '',time: "", county: ''})

const [bidAmount, setBidAmount] = React.useState("");

const handleBidAmount = (e: any) => {
  OpenBidShow();
}


React.useEffect(() => {
  if(authContext && authContext.userId ){
  (async function anyNameFunction() {
    const bidinfo = getServiceProviderBid(authContext.userId, data);
    if(bidinfo)  setBidAmount(bidinfo.price) 
   })();
  }
  
}, [authContext.userId]);




  const acceptTicket = async() =>{
    const authid = authContext.userId;

    // accept Job by employee
    if(setIsEmployee){

      const phone_number = authContext.user.attributes["phone_number"];
      const  email = authContext.user.attributes["email"];
      const ticketEmployeeInfo = JSON.stringify({
        email: email,
        phoneNumber: phone_number,
      });

        const response =  await API.graphql(
          graphqlOperation(assignExpertToTicket, {
            id: data.id,
            ticketEmployeeId: authid,
            ticketEmployeeInfo: ticketEmployeeInfo
          } )
      );
      console.log("accept ticket by employee", response);
      close(true, {...data, status: "assinged", ticketEmployeeId: authid });
      const responseLink =  await API.graphql(
        graphqlOperation(createUserTicket, { input: {
          id:  uuid(),
          ticketID : data.id,
          userID : authid
        } })
        );
    
    } // make form editable
    else if(formEdit) {
      setFormEdit(false)

    }// updated ticket by customer
    else if(formEdit == false){
      const response =  await API.graphql(
        graphqlOperation(updateTicket, { input: {
          id: data.id,
          price : values.price,
          time : values.time,
          title : values.title,
          content:  values.content ,
          county: values.county,
          destination : values.destination  ??  "sfsfsf" ,
          source : values.source  ?? "sfsfsf",
        } }
        
        )
    );
    console.log("update ticke4t info", {...data , ...values});
    close(true, {...data , ...values});
    }
  
}

const acceptBid = async(item: bidserviceProvider) => {
  
  const response =  await API.graphql(
    graphqlOperation(assignExpertToTicket, {
      id: data.id,
      price: item.price,
      ticketEmployeeId: item.serviceProvider,
      ticketEmployeeInfo: JSON.stringify({item})
    } )
);
console.log("accept ticket by employee", response);
close(true, {...data, status: "assinged", ticketEmployeeId: item.serviceProvider });

 };

const closeTicket = () => {
 setAlertType("closed")
 setOpenAlert(true)
};

const completeTicket = () => {
  setAlertType("completed")
  setOpenAlert(true)
 };
 
 const unassignTicket = () => {
  setAlertType("unassign")
  setOpenAlert(true)
 };


  const handleClose = () => {
    close(false,data);
  };

  const descriptionElementRef = React.useRef<HTMLElement>(null);
  React.useEffect(() => {
    if (openPop) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [openPop]);


  // second alert options

  const [openAlert, setOpenAlert] = React.useState(false)
  const [alertType, setAlertType] = React.useState("")

  const closeAlert = async(op: boolean, comments: string) => {

    if(op == true ){
      if ( alertType == "unassign"){
        const response =  await API.graphql(
          graphqlOperation(updateTicket, { input: {
            id: data.id,
            status: "open",
            ticketEmployeeId: null,
            ticketEmployeeInfo: null
          } }
          )
        );
      console.log("unassign close", response);
      close(true, {...data , ...values, status: "open"});
  
      } // completed or closed
      else {
        const response =  await API.graphql(
          graphqlOperation(updateTicket, { input: {
            id: data.id,
          status: alertType,
          closedComment: comments,
          closedTime: new Date().toUTCString()
          } }
          )
        );
      console.log("update ticke4t to close", response);
      close(true, {...data , ...values, status: alertType});
      }
  
    }else {
      setOpenAlert(false)
    }
 
  };

  // imageViewer
  const [openUserImage, setOpenUserImage] = React.useState(false)

  const imageListen = async() => {
    setOpenUserImage(true);
  }

  const closeAttachment =(op: boolean) =>{
    setOpenUserImage(false);
  }


    // expertView
    const [openExpert, setExpert] = React.useState(false)

    const expertShow = async() => {
      setExpert(true);
    }
  
    const closeExpertShow =(op: boolean) =>{
      setExpert(false);
    }

    const chatWithExpert = async() => {
      const currentId = authContext.userId;
      const otherId = data.ticketEmployeeId;
     const conv = await startConvoWithUser(data.ticketUserId, otherId, setIsEmployee );

     history.push({
      pathname: '/chat',
      state: { conversation: conv, user: currentId  }
    });

    }
    
    const sourceGoogle = (result: GeocoderResult[]) => {
      console.log("address",result);
      const value = stripAddress(result);
      setValues({...values, source: value.formatted_address})
    };
    
    const destGoogle = (result: GeocoderResult[]) => {
      console.log("address",result);
      const value = stripAddress(result);
      setValues({...values, destination: value.formatted_address, county: value.county})
    };


    //   bidVeiw
    const [openBid, setOpenBid] = React.useState(false)

    const OpenBidShow = () => {
      setOpenBid(true);
    }
  
    const CloseBidShow = async(op: boolean, execptedPrice: string, isNew: boolean) =>{
      setOpenBid(false);
      if(op === true ){
        console.log("execptedPrice",execptedPrice);
         await API.graphql(
          graphqlOperation(updateTicket, { input: {
            id: data.id,
            execptedPrice : execptedPrice
          } }
          ));
        if(isNew) {
              await API.graphql(
              graphqlOperation(createUserTicket, { input: {
                id:  uuid(),
                ticketID : data.id,
                userID : authContext.userId
              } })
              );    
        }
        close(true, {...data, execptedPrice: execptedPrice, status: "open" });
      }
    }

  return (
    <div>
      <Dialog
        open={openPop}
        fullScreen
        onClose={handleClose}
        scroll={scroll}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle id="scroll-dialog-title" onClose={handleClose} >Job Details</DialogTitle>
        <DialogContent dividers={scroll === 'paper'}>
        <Grid container spacing={1}>
        <Grid item xs={12} sm={6}>
          <TextField
                    disabled = {formEdit}

            required
            id="title"
            name="title"
            label="Title"
            fullWidth
            onChange={handleInputChange}
            value={values.title}
            autoComplete="title"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            disabled = {formEdit}
            required
            id="price"
            name="price"
            label="Price"
            fullWidth
            onChange={handleInputChange}
            value={values.price}
            autoComplete="price"
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          {
  formEdit== false ? (
    <LocationSearchInput label = "source" googleAddressReturn={sourceGoogle}
    />
  ): 
  <TextField label = "source" 
  name="source"
  fullWidth
  disabled = {true}
  defaultValue={data.source}
/>
}
          </Grid>

          <Grid item xs={12} sm={6}>

          {
  formEdit== false ? (
    <LocationSearchInput label = "destination" googleAddressReturn={destGoogle}
    />
  ): 
  <TextField label = "destination" 
  name="destination"
  fullWidth
  disabled = {true}
  defaultValue={data.destination}
/>
}
          </Grid>

        <Grid item xs={12}>
        {
  formEdit == false? (
    <TextField
required
id="time"
name="time"
label="time"
type="datetime-local"
fullWidth
onChange={handleInputChange}
value={new Date(values.time).toLocaleString()}
InputLabelProps={{
shrink: true,
}}
/>
  ): 
  <TextField label = "time" 
  name="time"
  fullWidth
  disabled = {true}
  defaultValue={new Date(values.time).toLocaleString()}
/>
}


        </Grid>
        <Grid item xs={12}>
        <TextField
          disabled = {formEdit}
          multiline
          rows="6"
          required
          id="content"
          name="content"
          label="content"
          fullWidth
          onChange={handleInputChange}
          value={values.content}
          autoComplete="content"
        />
        </Grid>

        { 
         bidAmount && setIsEmployee ?
        <Grid item xs={12} sm={6}>
          <TextField
            disabled = {true}
            required
            id="bidAmount"
            name="bidAmount"
            label="Bid Amount"
            fullWidth
            value={bidAmount}
            autoComplete="Enter Bid Amount"
          />
        </Grid> : null } 

        {
          data.status == "closed" || data.status == "completed" ? 
          <Grid item xs={12}>
          <TextField
            disabled = {formEdit}
            multiline
            rows="3"
            required
            id="closedComment"
            name="closedComment"
            label="closedComment"
            fullWidth
            value={closedComment}
            autoComplete="content"
          />
          </Grid>
          : null}      


        <Grid item xs={12} 
          container direction="row"
        >
        {
          data.ticketEmployeeId ?
          
         ( <div>
           <Chip
        icon={<FaceIcon />}
        label="Expert Info"
        clickable
        color="primary"
        onClick={expertShow}
        deleteIcon={<DoneIcon />}/>

        <Chip
        icon={<FaceIcon />}
        label="Start Chat"
        clickable
        color="primary"
        onClick={chatWithExpert}
        deleteIcon={<ChatIcon />}/>

         </div>
         )
        : null
        }
        { data.ownerUploadImages && JSON.parse(data.ownerUploadImages).length >0 ?
              <Chip
              label="See Attachments"
              clickable
              color="primary"
              onClick={imageListen}
              deleteIcon={<AttachmentIcon />}

            />  : null
        }
        </Grid> 

{
  data.status == "open" && setIsEmployee == false && data.ticketType === "Bid"? 

    <List 
    className={classes.root}
        component="nav"
        aria-labelledby="nested-list-subheader"
        subheader={
          <ListSubheader className={classes.subHeader} component="div" id="nested-list-subheader">
            Bids
          </ListSubheader>
        }
    >

    {bids.length > 0 ? bids.map(item => (
      <div key={item.serviceProvider} >
          <ListItem  key={item.serviceProvider}  alignItems="flex-start" >
          <ListItemText
        primary= {`Name: ${item.username}  Price: ${item.price} `} 
      />
         <Grid
      className={classes.buttonGird} 
        container
        direction="row"
        justify="flex-end"
        alignItems="flex-end"
      >  
      <Button  size="small" color="primary"variant="contained" onClick={()=> acceptBid(item)} >
            Accept 
          </Button>
    </Grid>
    </ListItem>
    <Divider variant="inset" component="li" />

      </div>  
    )):<div>No Jobs</div>}
    </List>
:null}

        <Grid
  container
  direction="row"
  justify="flex-end"
  alignItems="center"
>

</Grid>
      </Grid>
        </DialogContent>

        {
          data.status == "closed" || data.status == "completed" ? null :
        <DialogActions>
        { setIsEmployee == false?     
          
          <Button variant="contained" onClick={closeTicket} >
            Cancel Job
          </Button> :
            null}

         { setIsEmployee &&  data.status == "assinged"?     
          
          <Button variant="contained" onClick={unassignTicket} color="secondary">
            Unassign
          </Button> :
            null}

          { setIsEmployee &&  data.status == "assinged"?     
          
          <Button variant="contained" onClick={completeTicket} color="primary">
            Complete Job
          </Button> :
            null}
          {
            data.status == "open" && setIsEmployee == true ?
          <Button onClick={handleBidAmount}  variant="contained" color="primary">
          { data.ticketType === "Bid" ?"Bid" : "Accept"}
        </Button> : null
          }

          {
            data.status == "open" && setIsEmployee == false ?
          <Button onClick={acceptTicket}  variant="contained" color="primary">
          { formEdit == false ?"Update" : "Edit"}
        </Button> : null
          }
          {
            data.status == "assinged" && setIsEmployee == false ?
          <Button onClick={acceptTicket}  variant="contained" color="primary">
          Edit
        </Button> : null
          }
        </DialogActions> 
        }
      </Dialog>
      {openAlert? <AlertDialog  type={alertType} openPop={openAlert} closeAlert={closeAlert}/> : null}
      {openUserImage? <ImageViewer  inputimages={data.ownerUploadImages} openPop={openUserImage} closeAlert={closeAttachment}/> : null}
      {openExpert? <ExpertViewer  employee={data.ticketEmployeeId} openPop={openExpert} closeAlert={closeExpertShow}/> : null}
      {openBid? <BidDialog user= {user} id={authContext.userId} execptedPrice={data.execptedPrice} openPop={openBid} closeAlert={CloseBidShow}/> : null}

    </div>
  );
}

export default AcceptTicket;