import React, { useState, useEffect, useCallback } from 'react'
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import {makeStyles}  from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Snackbar from '../common/Snackbar'
import Divider from '@material-ui/core/Divider';

const useStyles = makeStyles(theme => ({
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
}));

const currencies = [
    {
      value: 'Paypal',
      label: 'Paypal',
    },
    {
      value: 'Venmo',
      label: 'Venmo',
    }
  ];

export interface PaymentFormProps {
  payment: string;
  nextButton: (value: string) => void
  backButton: () => void
}

const EmployeeBackgroundCheck: React.SFC<PaymentFormProps> = (props) => {

  const classes = useStyles();

  const handleInputChange = (e: any) => {
    const {name, value} = e.target
    if(name =="secondaryPaymentType" || name =="dob" || name =="secondaryPayment"){
      setValues({...values, [name]: value})
    }else {
      const re = /^[0-9\b]+$/;
      if (e.target.value === '' || re.test(e.target.value)) {
        setValues({...values, [name]: value})
      }
    }

    const {routingNumber, bankAccountNumber, ssn, reroutingNumber, rebankAccountNumber} = values
    if( name =="reroutingNumber" && value.length > 0){
     if( value != routingNumber){
      setErrorA({...errorA, [name]: value})
    }}else {
      setErrorA({...errorA, [name]: ''})
    }

    if( name =="rebankAccountNumber" && value.length > 0){
      if(value != bankAccountNumber){
        setErrorA({...errorA, [name]: value})
      }else {
        setErrorA({...errorA, [name]: ''})
      }
    }

    if( name =="ssn" && value.length > 0){
      if( value.length === 9){
        setErrorA({...errorA, [name]: ''})
      }else {
        setErrorA({...errorA, [name]: value})
      }
    }

    if( name =="secondaryPayment" && value.length > 0){
      const re = /\S+@\S+\.\S+/;
      if (re.test(e.target.value)) {
        setErrorA({...errorA, [name]: ''})
      }else {
        setErrorA({...errorA, [name]: value})
      }
    }

}

const [errorA, setErrorA] = useState({routingNumber: undefined, bankAccountNumber: '',ssn: '',
backgroundcheck: false,  secondaryPayment: '', secondaryPaymentType: '',dob: '',
reroutingNumber: '', rebankAccountNumber: ''
})

const handleNext = () => {
    const {routingNumber, bankAccountNumber, ssn, reroutingNumber, rebankAccountNumber} = values
    if(reroutingNumber !== routingNumber){
        setError("Routing number mismattched")
        setDisable(true)
        return
    }
    if(bankAccountNumber !== rebankAccountNumber){
        setError("Account number mismattched")
        setDisable(true)
        return
    }

    if(ssn.length < 2){
      setError("SSN Number missing")
      setDisable(true)
      return
  }

     console.log("value",values);
     props.nextButton(JSON.stringify(values));
}

useEffect(() => {
  if(props.payment) setValues(JSON.parse(props.payment))
}, [props.payment]);

 const [values, setValues] = useState({routingNumber: '', bankAccountNumber: '',ssn: '',
  backgroundcheck: false,  secondaryPayment: '', secondaryPaymentType: '',dob: '',
  reroutingNumber: '', rebankAccountNumber: ''
})

const [error, setError] = useState<string>('')

const setErrorMsg = useCallback((message: string) => {
    setError(message)
  }, [])

  const [disable, setDisable] = useState<boolean>(true)

  useEffect(() => {
    const {routingNumber, bankAccountNumber, ssn} = values;
    (routingNumber &&  bankAccountNumber && ssn ) ?  setDisable(false) :  setDisable(true)

}, [values ]);


  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Payment method
      </Typography>
      <Grid container spacing={1}>
        <Grid item xs={12} md={6}>
          <TextField required id="routingNumber" name="routingNumber"  label="Routing Number"     
                  onChange={handleInputChange}
            value={values.routingNumber} 
            fullWidth />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField required id="reroutingNumber" name="reroutingNumber"  label="Reenter Routing Number"     
                  onChange={handleInputChange}
                  error = {errorA.reroutingNumber.length > 1 ? true : undefined}
                  helperText = {errorA.reroutingNumber.length > 1 ? 'Routing number mismatch' : undefined}
            value={values.reroutingNumber} 
            fullWidth />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField required id="bankAccountNumber"  name="bankAccountNumber" label="Bank Account Number"
                         onChange={handleInputChange}
                         value={values.bankAccountNumber} 
          fullWidth />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField required id="rebankAccountNumber" name="rebankAccountNumber" 
          label="Reenter Bank Account Number" fullWidth
          onChange={handleInputChange}
          value={values.rebankAccountNumber} 
          error = {errorA.rebankAccountNumber.length > 1 ? true : undefined}
          helperText = {errorA.rebankAccountNumber.length > 1 ? 'Bank Account Number mismatached' : undefined}
          />
        </Grid>
        <Divider />

        <Grid item xs={12}>
        <Typography variant="h6" gutterBottom>
        SSN Number for Background Check
        </Typography>
          <TextField
            required
            id="ssn"
            name="ssn"
            label="Enter SSN for background Check"
            error = {errorA.ssn.length > 1 ? true : undefined}
            helperText = {errorA.ssn.length > 1 ? 'SSN should 9 digits' : undefined}
            fullWidth
            onChange={handleInputChange}
            value={values.ssn} 
          />
        </Grid>

        {/* <Grid item xs={12} md={6}>
          <TextField
            required
            id="dob"
            name="dob"
            label="Enter Your Date of Birth"
            helperText="Enter your date of birthday"
            fullWidth
            onChange={handleInputChange}
            value={values.dob} 
            type="date"
            InputLabelProps={{
              shrink: true,
            }}
          />
        </Grid> */}
        <Grid item xs={12} md={6}>
        <label >Enter Your Date of Birth:</label>
        <input
    type="date"
    className="input-group-field"
    name="dob"
    id="dob"
    max={new Date().toISOString().slice(0,10)}
    value={values.dob} 
    onChange={handleInputChange}
    />
</Grid>
        <Divider />

        <Grid item xs={12}>
        <Typography variant="h6" gutterBottom>
        Secondary Payment Method
        </Typography>
        <TextField
          id="secondaryPaymentType"
        name="secondaryPaymentType"
            label="SecondPaymentType"
          select
          value={values.secondaryPaymentType}
          onChange={handleInputChange}
          SelectProps={{
            native: true,
          }}
          helperText="Please select your currency"
          variant="outlined"
        >
          {currencies.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>

        </Grid>

        <Grid item xs={12}>
          <TextField
            required
            id="secondaryPayment"
            name="secondaryPayment"
            label="Enter Your Email for Secondary Payment"
            fullWidth
            onChange={handleInputChange}
            value={values.secondaryPayment} 
            error = {errorA.secondaryPayment.length > 1 ? true : undefined}
            helperText = {errorA.secondaryPayment.length > 1 ? 'Email Id in wrong format' : undefined}
          />
        </Grid>
        <Grid
  container
  direction="row"
  justify="flex-end"
  alignItems="center"
>
        <div className={classes.buttons}>
                <Button 
                 variant="contained"
                onClick={props.backButton} className={classes.button}>
                      Back
                    </Button>
                  <Button
                  disabled= {disable} 
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.button}
                  >
                   Save
                  </Button>
                   </div>
              </Grid>

      </Grid>

      <Snackbar
          variant='error'
          message={error}
          setMessage={message => setErrorMsg(message)}
        />

    </React.Fragment>
  );
}

export default EmployeeBackgroundCheck
