import React, { useState } from 'react'
import TextField from '@material-ui/core/TextField'
import InputAdornment from '@material-ui/core/InputAdornment';

export interface AuthPhoneFieldProps {
  setPhoneNumber: (phoneNumber: string) => void
}

const AuthPhoneField: React.SFC<AuthPhoneFieldProps> = ({ setPhoneNumber }) => {
  const [valid, setValid] = useState<boolean>(true)
  const [hint, setHint] = useState<string>('')

  let delay: any = null
  const validate = (phoneNumber: string): any => {
    if (delay !== null) {
      clearTimeout(delay)
    }
    delay = setTimeout(() => {
      const isValid = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im.test(phoneNumber)
      console.log("phone",phoneNumber);
      setValid(isValid)
      setHint(
        isValid ? '' : phoneNumber.length > 0 ? 'Invalid phone number.' : 'Enter phoneNumber.'
      )
      setPhoneNumber(isValid ? phoneNumber : '')
      delay = null
    }, 200)
  }

  return (
    <TextField
      error={!valid}
      variant='outlined'
      margin='normal'
      required
      fullWidth
      id='phoneNumber'
      label='Phone Number'
      name='phoneNumber'
      autoComplete='phoneNumber'
      helperText={hint}
      InputProps={{
        startAdornment: <InputAdornment position="start">+1</InputAdornment>,
      }}
      onChange={e => validate(e.currentTarget.value)}
    />
  )
}

export default AuthPhoneField
