import React, { useState, useCallback, useEffect } from 'react'
import { Auth, Hub } from 'aws-amplify'
import { useHistory } from 'react-router-dom'

export interface AuthContextProps {
  user: any
  provider: string
  name: string
  email: string,
  phoneNumber: string,
  userId: string,
  accountType: string,
  isAuthenticated(): boolean
  signUp(username: string, password: string, phoneNumber: string, accountType: string): Promise<any>
  confirmSignUp(phoneNumber: string, code: string): Promise<any>
  resendSignUp(phoneNumber: string): Promise<any>
  signIn(phoneNumber: string, password: string): Promise<any>
  signOut(): Promise<any>
  resetPassword(phoneNumber: string): Promise<any>
  confirmResetPassword(
    phoneNumber: string,
    newPassword: string,
    code: string
  ): Promise<any>
}

export const AuthContext = React.createContext<AuthContextProps>({
  user: null,
  provider: '',
  name: '',
  email: '',
  phoneNumber: '',
  userId: '',
  accountType: '',
  isAuthenticated: () => false,
  signUp: () => new Promise(reject => reject(0)),
  confirmSignUp: () => new Promise(reject => reject(0)),
  resendSignUp: () => new Promise(reject => reject(0)),
  signIn: () => new Promise(reject => reject(0)),
  signOut: () => new Promise(reject => reject(0)),
  resetPassword: () => new Promise(reject => reject(0)),
  confirmResetPassword: () => new Promise(reject => reject(0))
})

export interface AuthContextProviderProps {
  children: any
}

const AuthContextProvider: React.SFC<AuthContextProviderProps> = ({
  children
}) => {
  const [user, setUser] = useState<any>(null)
  const [provider, setProvider] = useState<string>('')
  const [email, setEmail] = useState<string>('')
  const [name, setName] = useState<string>('')
  const [phoneNumber, setphoneNumber] = useState<string>('')
  const [userId, setUserId] = useState<string>('')
  const [accountType, setAccountType] = useState<string>('')

  const history = useHistory()

  useEffect(() => {
    console.log('checking for authenticated user...')
    getUserData()
  }, [])

  useEffect(() => {
    Hub.listen('auth', data => {
      const { payload } = data
      switch (payload.event) {
        case 'signIn':
          getUserData()
          history.push('/')
          break
        case 'signOut':
          setUser(null)
          setProvider('')
          localStorage.setItem('provider', '')
          setEmail('')
          setName('')
          setphoneNumber('')
          setUserId('')
          history.push('/')
          break
      }
    })
  }, [])

  const getUserData = () => {
    Auth.currentAuthenticatedUser()
      .then(data => {
        console.log('current', data)
        setUser(data)
        const email1 = data.email || data.attributes.email || ''
        const name1 = data.name || data.attributes.email || ''
        const provider1 = localStorage.getItem('provider') || ''

        setUserId(data.username)
        setEmail(email1)
        setName(name1)
        setProvider(provider1)
        setAccountType(data.attributes["custom:account_type"])

        // console.log('email:', email1, email)
        // console.log('name:', name1)
        // console.log('provider:', provider1)
         console.log('provider:', accountType,data.attributes, data.attributes["custom:account_type"])


      })
      .catch(err => {
        console.log('no current authenticated user')
      })
  }

  const isAuthenticated = (): boolean => user !== null

  const signUp = useCallback((userEmail: string, password: string, phoneNumber: string, accountType: string) => {
    return new Promise(async (resolve, reject) => {
      try {
        const { user } = await Auth.signUp({
          username: "+1"+phoneNumber,
          password: password,
          attributes: {
            email: userEmail,          
            phone_number: "+1"+phoneNumber,
            'custom:account_type': accountType
        },
        })
        console.log(user)
        resolve(user)
      } catch (err) {
        console.log(err)
        let message: string = 'An internal error occurred.'
        switch (err.code) {
          case 'UserNotConfirmedException':
          case 'PasswordResetRequiredException':
          case 'NotAuthorizedException':
          case 'UserNotFoundException':
          case 'UsernameExistsException':
            message = err.message
            break
          default:
            console.log(err)
            break
        }
        reject(message)
      }
    })
  }, [])

  const confirmSignUp = useCallback((phoneNumber: string, code: string) => {
    console.log('confirm sign up', phoneNumber, code)
    return new Promise(async (resolve, reject) => {
      try {
        const { user } = await Auth.confirmSignUp("+1"+phoneNumber, code)
        resolve(user)
      } catch (err) {
        const message = err.message || 'An internal error occurred.'
        reject(message)
      }
    })
  }, [])

  const signIn = useCallback((phoneNumber: string, password: string) => {
    console.log('sign in', phoneNumber, password)
    return new Promise(async (resolve, reject) => {
      try {
        const data = await Auth.signIn({
          username: "+1"+phoneNumber,
          password: password
        })
        localStorage.setItem('provider', 'email')
        console.log(data)
        resolve(data)
      } catch (err) {
        const message = err.message || 'An internal error occurred.'
        reject(message)
      }
    })
  }, [])

  const resendSignUp = useCallback((phoneNumber: string) => {
    console.log('resend sign up', phoneNumber)
    return new Promise((resolve, reject) => {
      Auth.resendSignUp("+1"+phoneNumber)
        .then(data => {
          resolve(data)
        })
        .catch(err => {
          const message = err.message || 'An internal error occurred.'
          reject(message)
        })
    })
  }, [])

  const signOut = useCallback(() => {
    console.log('sign out')
    return new Promise((resolve, reject) => {
      Auth.signOut()
        .then(data => {
          resolve(data)
        })
        .catch(err => {
          const message = err.message || 'An internal error occurred.'
          reject(message)
        })
    })
  }, [])

  const resetPassword = useCallback((phoneNumber: string) => {
    console.log('reset password', phoneNumber)
    return new Promise((resolve, reject) => {
      Auth.forgotPassword("+1"+phoneNumber)
        .then(data => {
          resolve(data)
        })
        .catch(err => {
          const message = err.message || 'An internal error occurred.'
          reject(message)
        })
    })
  }, [])

  const confirmResetPassword = useCallback(
    (phoneNumber: string, newPassword: string, code: string) => {
      console.log('confirm reset password', phoneNumber, code, newPassword)
      return new Promise((resolve, reject) => {
        Auth.forgotPasswordSubmit("+1"+phoneNumber, code, newPassword)
          .then(data => resolve(data))
          .catch(err => {
            const message = err.message || 'An internal error occurred.'
            reject(message)
          })
      })
    },
    []
  )

  return (
    <AuthContext.Provider
      value={{
        user: user,
        provider: provider,
        name: name,
        email: email,
        phoneNumber: phoneNumber,
        accountType: accountType,
        userId: userId,
        isAuthenticated: isAuthenticated,
        signUp: signUp,
        confirmSignUp: confirmSignUp,
        resendSignUp: resendSignUp,
        signIn: signIn,
        signOut: signOut,
        resetPassword: resetPassword,
        confirmResetPassword: confirmResetPassword
      }}>
      {children}
    </AuthContext.Provider>
  )
}

export default AuthContextProvider
