import React, { useContext } from 'react'
import { useHistory } from 'react-router-dom'
import {
  useTheme,
  createStyles,
  makeStyles,
  Theme
} from '@material-ui/core/styles'

import { Toolbar, AppBar, IconButton } from '@material-ui/core'
import HomeIcon from '@material-ui/icons/Home'
import NotificationsIcon from '@material-ui/icons/Notifications';
import { AuthContext } from '../auth/auth-context'
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import { API, graphqlOperation } from 'aws-amplify'
import { getUserMessages } from '../graphql/queries'
import Badge from '@material-ui/core/Badge';
import { startConvoWithUser} from '../../src/ticket/util';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    appBar: { boxShadow: 'none', backgroundColor: 'transparent' },
    appBarBefore: { backgroundColor: '#fff', border: 1 },

    menuButton: {
      marginRight: theme.spacing(1)
    },
    spacer: {
      flexGrow: 1
    }
  })
)

export interface HeaderProps {
  header?: string;
}

 interface messages {
  id: string,
  isSent: string,
  owner : string,
}

const Header: React.SFC<HeaderProps> = ({ header }) => {
  const classes = useStyles(useTheme())
  const authContext = useContext(AuthContext);
  const history = useHistory()

  const [contactFlag, setContactFlag] = React.useState(false);
  const [chatFlag, setChatFlag] = React.useState(false);


  React.useEffect(() => {
    if(header && authContext && authContext.userId && authContext.user ){

      const id = authContext.userId;
    (async function anyNameFunction() {
      const IgetUserMessages = await API.graphql(graphqlOperation(getUserMessages, { receiver: id, isSent: "false" }))
      console.log("getUserMessages", IgetUserMessages);
       //@ts-ignore
      if(IgetUserMessages.data.messageByReceiver && IgetUserMessages.data.messageByReceiver.items.length > 0){
        setChatFlag(true);
        //@ts-ignore
        const messages = IgetUserMessages.data.messageByReceiver.items as messages[];
        const customercare = messages.filter((message:any) => message.owner === "1f011946-f3b8-4fec-8ed3-276ef522e563");
        // 1f011946-f3b8-4fec-8ed3-276ef522e563
        if(customercare.length > 0 )setContactFlag(true);

      }
     })();
    }
    
  }, [header, authContext.userId]);

  const chatWithExpert = async() => {
    const currentId = authContext.userId;
    const otherId = "1f011946-f3b8-4fec-8ed3-276ef522e563";
   const conv = await startConvoWithUser(currentId, otherId, true );

   history.push({
    pathname: '/chat',
    state: { conversation: conv, user: currentId  }
  });

  }

  let authLink = authContext.isAuthenticated()  && header ? (
    <div>
     { header === "profile" ? <IconButton  color="inherit"
    className={classes.menuButton}
    onClick={() => authContext.signOut()}>
    <ExitToAppIcon color='action'/>
</IconButton>  : null}

    { header === "Home" ? <span> 


<IconButton  color="inherit"
    className={classes.menuButton}
    onClick={() => history.push('/checkout')}>
    <AccountCircleIcon color='action'/>
</IconButton> 

<IconButton  color="inherit"
    className={classes.menuButton}
    onClick={chatWithExpert}>
            <Badge color="secondary" overlap="circle" badgeContent=" "
            invisible={contactFlag? false: true}
            >
    <ContactSupportIcon color='action'/>
    </Badge>

</IconButton> 

<IconButton aria-label="show 17 new notifications" color="inherit"
    className={classes.menuButton}
    onClick={() => history.push('/chat')}
>
<Badge color="secondary" overlap="circle" badgeContent=" "
invisible={chatFlag? false: true}
>
    <NotificationsIcon color='action'/>
    </Badge>

</IconButton>

</span>: null
}
    </div>
  ) : null

  return (
    <AppBar position='fixed' className={authContext.isAuthenticated() ? classes.appBarBefore :classes.appBar}>
      <Toolbar>
        <IconButton
          edge='start'
          className={classes.menuButton}
          onClick={() => history.push('/')}
          color='inherit'
          aria-label='home'>
          <HomeIcon color='action' />
        </IconButton>
        <div className={classes.spacer}></div>
        {authLink}
      </Toolbar>
    </AppBar>
  )
}

export default Header
